.circle {
    width: 10vw;
    height: 10vw;
    max-width: 100px;
    max-height: 100px;
    border-radius: 50%;
    margin: 0;
    cursor: pointer;
    transition: all 0.2s;
    display: inline-block;
    box-sizing: border-box;
    margin: 15px;
    box-shadow: 0 1px 5px rgba(95, 95, 95, 0.5);
    box-shadow: inset 0 -1px 5px rgba(0, 0, 0, 0.3), 0 1px 5px rgba(95, 95, 95, 0.429);
}

.circle.pink {
    background-color: #D870AD;
}

.circle.green {
    background-color: #45B649;
}

.circle.blue {
    background-color: #357ABD;
}

.circle.yellow {
    background-color: #FFD700;
}

.circle:active {
    transform: scale(0.8);
}


@media only screen and (max-width: 768px) {
  
    .circle {
      width: 7em;
      height: 7em;
      flex: 1 50%;
    }
  }